import React, { useState } from 'react';
import { FaFilePdf, FaDownload, FaTimes } from 'react-icons/fa';
import RelatorioPDF from './RelatorioPDF';
import { formatarNomeArquivo } from '../utils/formatters';
import '../App.css';
import { formatarData } from '../utils/formatters';

function ModalRelatorio({ isOpen, onClose, receitas, despesas }) {
  const [tipoRelatorio, setTipoRelatorio] = useState('consolidado');
  const [dataInicio, setDataInicio] = useState('');
  const [dataFim, setDataFim] = useState('');

  const definirPeriodoPadrao = (tipo) => {
    const hoje = new Date();
    let inicio, fim;

    switch (tipo) {
      case 'mes_atual':
        inicio = new Date(hoje.getFullYear(), hoje.getMonth(), 1);
        fim = new Date(hoje.getFullYear(), hoje.getMonth() + 1, 0);
        break;
      case 'mes_anterior':
        inicio = new Date(hoje.getFullYear(), hoje.getMonth() - 1, 1);
        fim = new Date(hoje.getFullYear(), hoje.getMonth(), 0);
        break;
      case 'ultimo_trimestre':
        inicio = new Date(hoje.getFullYear(), hoje.getMonth() - 3, 1);
        fim = hoje;
        break;
      case 'ultimo_ano':
        inicio = new Date(hoje.getFullYear() - 1, hoje.getMonth(), hoje.getDate());
        fim = hoje;
        break;
      default:
        return;
    }

    setDataInicio(inicio.toISOString().split('T')[0]);
    setDataFim(fim.toISOString().split('T')[0]);
  };

  if (!isOpen) return null;

  const gerarRelatorio = () => {
    if (!dataInicio || !dataFim) {
      alert('Por favor, selecione as datas de incio e fim do perodo.');
      return;
    }

    if (new Date(dataInicio) > new Date(dataFim)) {
      alert('A data de incio no pode ser maior que a data de fim.');
      return;
    }

    const periodo = `${formatarData(dataInicio)} a ${formatarData(dataFim)}`;
    const relatorio = new RelatorioPDF();
    let doc;

    switch (tipoRelatorio) {
      case 'receitas':
        doc = relatorio.generateReceitasReport(receitas, periodo);
        break;
      case 'despesas':
        doc = relatorio.generateDespesasReport(despesas, periodo);
        break;
      case 'consolidado':
        doc = relatorio.generateConsolidatedReport(receitas, despesas, periodo);
        break;
      case 'categoria':
        doc = relatorio.generateCategoryReport(receitas, despesas, periodo);
        break;
      default:
        return;
    }

    // O relatrio ser aberto em uma nova janela
    // O usurio pode usar Ctrl+P para imprimir/salvar como PDF
    onClose();
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <div className="modal-header">
          <h3>
            <FaFilePdf /> Gerar Relatrio
          </h3>
          <button onClick={onClose} className="modal-close">
            <FaTimes />
          </button>
        </div>

        <div className="modal-body">
          <div className="form-group">
            <label>Tipo de Relatrio:</label>
            <select 
              value={tipoRelatorio} 
              onChange={(e) => setTipoRelatorio(e.target.value)}
              className="form-select"
            >
              <option value="consolidado">Relatrio Consolidado</option>
              <option value="receitas">Relatrio de Receitas</option>
              <option value="despesas">Relatrio de Despesas</option>
              <option value="categoria">Relatrio por Categoria</option>
            </select>
          </div>

                                <div className="form-group">
                        <label>Data de Incio:</label>
                        <input
                          type="date"
                          value={dataInicio}
                          onChange={(e) => setDataInicio(e.target.value)}
                          className="form-select"
                          max={new Date().toISOString().split('T')[0]}
                        />
                      </div>

                      <div className="form-group">
                        <label>Data de Fim:</label>
                        <input
                          type="date"
                          value={dataFim}
                          onChange={(e) => setDataFim(e.target.value)}
                          className="form-select"
                          max={new Date().toISOString().split('T')[0]}
                        />
                      </div>

                      <div style={{
                        display: 'flex',
                        gap: '8px',
                        marginTop: '10px',
                        flexWrap: 'wrap'
                      }}>
                        <button
                          type="button"
                          onClick={() => definirPeriodoPadrao('mes_atual')}
                          style={{
                            padding: '6px 12px',
                            fontSize: '11px',
                            background: '#007bff',
                            color: 'white',
                            border: 'none',
                            borderRadius: '4px',
                            cursor: 'pointer'
                          }}
                        >
                          Ms Atual
                        </button>
                        <button
                          type="button"
                          onClick={() => definirPeriodoPadrao('mes_anterior')}
                          style={{
                            padding: '6px 12px',
                            fontSize: '11px',
                            background: '#6c757d',
                            color: 'white',
                            border: 'none',
                            borderRadius: '4px',
                            cursor: 'pointer'
                          }}
                        >
                          Ms Anterior
                        </button>
                        <button
                          type="button"
                          onClick={() => definirPeriodoPadrao('ultimo_trimestre')}
                          style={{
                            padding: '6px 12px',
                            fontSize: '11px',
                            background: '#28a745',
                            color: 'white',
                            border: 'none',
                            borderRadius: '4px',
                            cursor: 'pointer'
                          }}
                        >
                          ltimo Trimestre
                        </button>
                        <button
                          type="button"
                          onClick={() => definirPeriodoPadrao('ultimo_ano')}
                          style={{
                            padding: '6px 12px',
                            fontSize: '11px',
                            background: '#dc3545',
                            color: 'white',
                            border: 'none',
                            borderRadius: '4px',
                            cursor: 'pointer'
                          }}
                        >
                          ltimo Ano
                        </button>
                      </div>

                      {dataInicio && dataFim && (
                        <div style={{
                          background: '#e8f5e8',
                          padding: '10px',
                          borderRadius: '5px',
                          marginTop: '10px',
                          fontSize: '12px',
                          color: '#2d5a2d'
                        }}>
                          <strong>Perodo selecionado:</strong> {formatarData(dataInicio)} a {formatarData(dataFim)}
                        </div>
                      )}

          <div className="relatorio-info">
            <h4>Informaes do Relatrio:</h4>
            <ul>
              <li><strong>Consolidado:</strong> Receitas e despesas com resumo financeiro</li>
              <li><strong>Receitas:</strong> Lista detalhada de todas as receitas</li>
              <li><strong>Despesas:</strong> Lista detalhada de todas as despesas</li>
              <li><strong>Categoria:</strong> Agrupamento por tipo de receita/despesa</li>
            </ul>
            <p style={{marginTop: '10px', fontSize: '12px', color: '#666'}}>
              <strong>Como usar:</strong> Selecione o perodo desejado e clique em "Gerar Relatrio". 
              O relatrio ser aberto em uma nova janela. Use Ctrl+P para imprimir ou salvar como PDF.
            </p>
          </div>
        </div>

        <div className="modal-footer">
          <button onClick={onClose} className="btn-cancelar">
            Cancelar
          </button>
                    <button
            onClick={gerarRelatorio}
            className="btn-gerar-relatorio"
            disabled={!dataInicio || !dataFim}
          >
            <FaDownload /> Gerar Relatrio
          </button>
        </div>
      </div>
    </div>
  );
}

export default ModalRelatorio; 